<script>
import "animate.css"
export default {
    name:'ClassLog',
    data(){
        return{
            message:[],
            total:0,
            imgList:[
                'http://www.tinyxu.com/static/cover/4ecfb12fd82263bd08a128ff692ffedd.jpg',
                'http://www.tinyxu.com/static/cover/73deb6f1e530c0434308896bfd0294bc.png',
                'http://www.tinyxu.com/static/cover/cb9c67c3b74ff62b51fbfa8240750a3e.jpg',
                'http://www.tinyxu.com/static/cover/968cce8a7fc2f14f5bafb3bab46dd6a0.jpg',
            ]
        }
    },
    methods:{
        getPage(page){
            this.$axios({
                method:'get',
                url:'/classLog',
                params: {page}
            }).then(res=>{
            this.message = res.data.data
            })
        },
        logOut(){
            localStorage.removeItem("token");
            this.$router.push('/login')
        }
    },
    computed:{
        isLogin(){
            return  localStorage.getItem("token") =='44b4997c9a42b02e2ed3056fcdc80878';
        }

    },
    mounted(){
        this.$axios({
            method:'get',
            url:'/classLog',
            params: {page:1}
        }).then(res=>{
           this.message = res.data.data
        })
         this.$axios({
            method:'get',
            url:'/allLog'
        }).then(res=>{
           this.total = res.data.data[0].count
        })
    }
    
}

</script>

